export class ImageStore {
  private static _instance: ImageStore | undefined;

  private cacheImages: { [key in string]: string } = {};

  public static getInstance(): ImageStore {
    if (!this._instance) {
      this._instance = new ImageStore();
    }
    return this._instance;
  }

  public static clear() {
    if (this._instance) delete this._instance;
  }

  addNode({ data, url }: { data: string; url: string }): void {
    this.cacheImages[url] = data;
  }

  removeNode(url: string) {
    delete this.cacheImages?.[url];
  }

  getImage(key: string): string {
    return this.cacheImages[key] || key;
  }
}
