import * as work from 'webworkify-webpack';
import { EventEmitter } from 'eventemitter3';
import { ImageStore } from './image.store';
import WorkerEvent from './event';

export default class WorkerManager extends EventEmitter {
  private static _instance: WorkerManager | undefined;

  private _workerImage_: any = null;

  private cbImg: Function | null = null;

  public static getInstance(): WorkerManager {
    if (!this._instance) {
      this._instance = new WorkerManager();
    }
    return this._instance;
  }

  public static clear(): void {
    this._instance?.destroy();
    delete this._instance;
  }

  constructor() {
    super();
    this._init();
  }

  private _init() {
    if (!this._workerImage_) {
      this._workerImage_ = work(require.resolve('./image.worker.ts'));
      this.addEventListenerWorker();
    }
  }

  public getObjectURL(): string {
    return this._workerImage_?.objectURL || '';
  }

  private addEventListenerWorker() {
    this.cbImg = (event: { data: any }) => {
      switch (event.data.event) {
        case WorkerEvent.loadImageSuccess:
          ImageStore.getInstance().addNode({
            data: event?.data?.data?.data,
            url: event?.data?.data?.url,
          });
          this.emit(WorkerEvent.loadImageSuccess, event.data);
          break;
        case WorkerEvent.loadImageError:
          ImageStore.getInstance().removeNode(event?.data?.data?.url);
          this.emit(WorkerEvent.loadImageError, event.data);
          break;
        default:
          break;
      }
    };
    this._workerImage_?.addEventListener('message', this.cbImg);
  }

  public loadImage(url: string) {
    this._workerImage_?.postMessage({ cmd: WorkerEvent.loadImage, url });
  }

  public preloadImages(urls: string[]) {
    this._workerImage_?.postMessage({
      cmd: WorkerEvent.preLoadImage,
      urls,
    });
  }

  private destroy() {
    this.cbImg &&
      this._workerImage_?.removeEventListener('message', this.cbImg);
    this._workerImage_?.terminate();
    this.removeAllListeners();
  }
}
