import { fetch } from 'whatwg-fetch';
import WorkerEvent from './event';

const cacheList: { [key in string]: number } = {};

export default function ImageWorker(self: any) {
  const forwardMessage = (ev: any, data: any) => {
    self.postMessage({ event: ev, data });
  };
  self.addEventListener('message', (ev: any) => {
    const { data } = ev;
    switch (data.cmd) {
      case WorkerEvent.loadImage:
        if (cacheList[data?.url] === 1) {
          return;
        }
        loadImage(data?.url)
          .then((data1) => {
            forwardMessage(WorkerEvent.loadImageSuccess, {
              url: data?.url,
              data: data1,
            });
          })
          .catch((err) => {
            forwardMessage(WorkerEvent.loadImageError, { url: data?.url });
          });
        break;
      case WorkerEvent.preLoadImage:
        data?.urls?.map((url: string) => {
          if (cacheList[url] === 1) {
            return;
          }
          loadImage(url).then((data1) => {
            forwardMessage(WorkerEvent.loadImageSuccess, {
              url,
              data: data1,
            });
          });
        });
        break;
      default:
        break;
    }
  });
}

const loadImage = async (url: string) => {
  try {
    cacheList[url] = 1;
    const response = await fetch(url);
    const fileBlob = await response.blob();
    delete cacheList[url];
    const URLLOAD = URL || webkitURL;
    if (fileBlob.type.indexOf('image') !== -1) {
      return URLLOAD.createObjectURL(fileBlob);
    }
    throw new Error('Lỗi load Blob');
  } catch (e) {
    delete cacheList[url];
    throw e;
  }
};
